.app__heading {
  font-family: impact;
  text-align: center;
  background-image: -webkit-gradient( linear,
    left top, left bottom,
    from(rgb(201, 167, 63)),
    to(rgb(207, 82, 82)));
  background-image: -webkit-linear-gradient( rgb(201, 167, 63),
    rgb(207, 82, 82));
  background-image: -o-linear-gradient( rgb(201, 167, 63),
    rgb(207, 82, 82));
  background-image: linear-gradient( rgb(201, 167, 63),
    rgb(207, 82, 82));
  -webkit-background-clip: text;
  color: transparent;
  font-size: 5rem;
}



