    from(rgb(201, 167, 63)),
    to(rgb(207, 82, 82))
  );
  background-image: -webkit-linear-gradient(
    left,
    rgb(201, 167, 63),
    rgb(207, 82, 82)
  );
  background-image: -o-linear-gradient(
    left,
    rgb(201, 167, 63),
    rgb(207, 82, 82)
  );
  background-image: linear-gradient(
    to right,
    rgb(201, 167, 63),
    rgb(207, 82, 82)
  );
  border-radius: 5px;
  padding: 1rem;
  margin: 1rem;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.card:first-child {
  margin-left: 0;
}

.card:hover {
  -webkit-transform: translateY(-.3rem);
      -ms-transform: translateY(-.3rem);
          transform: translateY(-.3rem);
}

.card img {
  width: 100%;
  margin-bottom: 2rem;
}



